"use client"

import { useAuth } from "@/lib/auth-context"
import { useRouter } from "next/navigation"
import { useEffect } from "react"
import { EducatorDashboardOverview } from "@/components/educator/dashboard-overview"
import { AdminDashboardOverview } from "@/components/admin/dashboard-overview"
import { DashboardLayout } from "@/components/layout/dashboard-layout"

export default function DashboardPage() {
  const { user, isLoading } = useAuth()
  const router = useRouter()

  useEffect(() => {
    if (!isLoading && user) {
      if (user.role === "student") {
        router.push("/dashboard/student")
      } else if (user.role === "analyst") {
        router.push("/dashboard/analyst")
      }
    }
  }, [user, isLoading, router])

  if (isLoading || !user) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin">
          <div className="w-12 h-12 border-4 border-gray-200 border-t-blue-600 rounded-full"></div>
        </div>
      </div>
    )
  }

  if (user.role === "admin") {
    return (
      <DashboardLayout>
        <AdminDashboardOverview />
      </DashboardLayout>
    )
  }

  if (user.role === "educator") {
    return (
      <DashboardLayout>
        <EducatorDashboardOverview />
      </DashboardLayout>
    )
  }

  return null
}
